import * as types from './types'


const mutations = {
    [types.GET_MSITE](state, res) {
       state.msiteList = res;
    },
    [types.GET_SHOP](state, res) {
        state.shopList = res;
    },
    //加入购物车
    [types.SET_CART](state, {
        car_id,id,title,pic,number,good,price,buynum
        }) {
            let cart = state.cart;
            if (cart[car_id]){
                cart[car_id]['buynum']++; 
            } else {
                cart[car_id] = {
                    id, title, pic, number, good, price, buynum
            }
            state.cart = {...cart};
            }
    },//移出购物车
    [types.SET_CART_OUT](state, {
        car_id, id, title, pic, number, good, price, buynum
        }) {
            let cart = state.cart;
            if (cart[car_id]) {
                if (cart[car_id]['buynum']>1) {
                    cart[car_id]['buynum']--;
                    state.cart = { ...cart };
                } else {
                    cart[car_id]['buynum']--;
                    delete cart[car_id];
                }
                state.cart = { ...cart };
            }
    },//清空购物车（没生成订单）
    [types.INIT_CART](state) {
        state.cart = {};
    },//生成订单
    [types.SET_ORDER](state,data) {
        state.order.push(data);
    },
    // 记录用户信息
    [types.RECORD_USERINFO](state, info) {

        state.userInfo = info;
        state.login = true;
        setStore('user_id', info.user_id);
    },
    //获取用户信息存入vuex
    [types.GET_USERINFO](state, info) {

        if (state.userInfo && (state.userInfo.username !== info.username)) {
        return;
        };
        if (!state.login) {
        return
        }
        if (!info.message) {
        state.userInfo = { ...info
        };
        } else {
        state.userInfo = null;
        }
    },
    //手机登陆
    [types.SET_LOGIN](state, data) {
        if (data.phone === '0000') {
            state.userInfo = data.phoneNumber;
            state.logined = true;
        } else {
            alert('验证码不对')
        }
    },


}

export default mutations


