import Vue from 'vue'
import Router from 'vue-router'

const Index = () => import('@/pages/Index')
const Food = () => import('@/pages/nav/food')
const NewUser = () => import('@/pages/new-user')
const Red = () => import('@/pages/red')
const Discover = () => import('@/pages/discover')
const Order = () => import('@/pages/order')
const Shop = () => import('@/pages/Shop')
const Login = () => import('@/pages/Login')
const Profile = () => import('@/pages/Profile')


Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      redirect:'/msite'
    },
    {
      path: '/msite',
      name: 'Index',
      component: Index
    },
    {
      path: '/msite/food',
      name: 'Food',
      component: Food
    },
    {
      path: '/newuser',
      name: 'NewUser',
      component: NewUser
    },
    {
      path: '/red',
      name: 'Red',
      component: Red
    },
    {
      path: '/discover',
      name: 'Discover',
      component: Discover
    },
    {
      path: '/shop',
      name: 'Shop',
      component: Shop
    },
    {
      path: '/order',
      name: 'order',
      component: Order
    },
    {
      path: '/login',
      name: 'Login',
      component: Login
    },
    {
      path: '/profile',
      name: 'Profile',
      component: Profile
    }
  ],
  scrollBehavior(to, from, savedPosition) {
    return { x: 0, y: 0 }
  }
})



