import api from '@/axios/api.js'
import '@/mock.js'
import * as types from './types'

const actions = {
    //首页list
    async getMsite({commit,state}) {
        api.getlist('/indexlist').then(res => {
            commit(types.GET_MSITE, res.list)
        })
    },
    //详情list
    async getShop({ commit, state }) {
        api.getlist('/shoplist').then(res => {
            commit(types.GET_SHOP, res.list)
        })
    },


}

export default actions


