import Vue from 'vue'
import Vuex from 'vuex'
import mutations from './mutation'
import actions from './action'

Vue.use(Vuex)

const state = {
  msiteList: [], //首页list
  shopList: [], //详情list
  cart:{},//购物车
  order:[],
  userInfo: null, //用户信息
  logined: false,//是否登录
}
const getters = {
  cartList: (state) => {
    return Object.values(state.cart);
  },//购物车
  cartNumber:(state,getters) =>{
    let num = null;
    for (let key of getters.cartList) {
      num +=key.buynum; 
    }
    return num;
  },
  cartPrice: (state, getters) => {
    let price = null;
    for (let key of getters.cartList) {
      price += key.price*key.buynum;
    }
    return price;
  },
  cartTotal:(state, getters) => {
    let cartTotal = {};
    cartTotal.list = getters.cartList;
    cartTotal.number = getters.cartNumber;
    cartTotal.price = getters.cartPrice;
    return cartTotal;
  },


}
export default new Vuex.Store({
  state,
  getters,
  actions,
  mutations
})



